
$google-blue: #4285f4;
$button-active-blue: #1669f2;

.google-btn {
    cursor: pointer;
    margin: 20px 0;
    width: 100%;
    height: 30px;
    background-color: $google-blue;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);

    transition: box-shadow .3s ease;

    .google-icon-wrapper {
        position: absolute;
        margin-top: 1px;
        margin-left: 1px;
        width: 28px;
        height: 28px;
        background-color: $white;
    }
    .google-icon {
        margin-top: 5px;
        margin-left: 5px;
        width: 18px;
        height: 18px;
    }
    .btn-text {
        float: right;
        margin: 7px 40px 0 0;
        color: $white;
        font-size: 14px;
        letter-spacing: 0.2px;
    }
    &:hover {
        box-shadow: 0 0 6px $google-blue;
    }
    &:active {
        background: $button-active-blue;
    }
}